import { spawn } from 'child_process'
import { createWriteStream as stream } from 'fs'
import { pick, omit } from 'lodash'
import yargs from 'yargs'

export function shell(command, options = {}, handle){
  if (typeof options === 'function') {
     handle = options
     options = {}
  }

  let [cmd, ...args] = command.split(' ')

  let proc = spawn(cmd, args, options)

  proc.stdout.on('data', (data) => handle(data.toString()))
  proc.stderr.on('data', (data) => handle(data.toString()))
  proc.on('error', (data) => handle(data.toString()))

  return proc
}



